body {
  font-family: Arial;
  background: #f4f4f4;
  margin: 0;
  padding: 20px;
}
.form-container, .success-container {
  background: white;
  padding: 20px;
  max-width: 500px;
  margin: auto;
  border-radius: 8px;
}
label {
  display: block;
  margin-top: 10px;
}
input, select {
  width: 100%;
  padding: 8px;
  margin-top: 4px;
}
.error {
  color: red;
  font-size: 0.8em;
}
button {
  margin-top: 20px;
  padding: 10px 20px;
}
